<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true">添加角色</el-button>
        </el-col>
      </el-row>

      <!-- 添加角色 -->
      <el-dialog
        title="添加角色"
        :visible.sync="addDialogVisible"
        width="50%"
        @close="addDialogClosed"
        >
        <!-- 内容主题 -->
        <el-form :model="roleForm" :rules="addRoleRules" ref="addRoleFormRef" label-width="100px">
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="roleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="roleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!-- 底部 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addrole">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 修改角色 -->
      <el-dialog
        title="修改角色"
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
        >
        <!-- 内容主题 -->
        <el-form :model="editRoleForm" :rules="addRoleRules" ref="editRoleFormRef" label-width="100px">
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="editRoleForm.roleName" ></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="editRoleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!-- 底部 -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="editRoleInfo">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 角色列表区域 -->
      <el-table border stripe :data="rolelist">
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级和三级权限 -->
              <el-col :span="19">
                <!-- 通过for循环嵌套 渲染二级权限 -->
                <el-row :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']" v-for="(item2, i2) in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag closable @close="removeRightById(scope.row, item3.id)" type="warning" v-for="(item3) in item2.children" :key="item3.id">
                      {{item3.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>

          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <!-- 修改 -->
            <el-button  type="primary" size="mini" icon="el-icon-edit" @click="showEditDialog(scope.row.id)">修改</el-button>
            <!-- 删除 -->
            <el-button @click="removeRoleById(scope.row.id)"  type="danger" size="mini" icon="el-icon-delete">删除</el-button>
            <!-- 分配角色 -->
            <el-tooltip  :enterable="false" effect="dark" content="分配权限" placement="top">
              <el-button type="warning" size="mini" icon="el-icon-setting" @click="showSetRightDialog(scope.row)">分配权限</el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 分配权限对话框 -->
    <el-dialog
      title="分配权限"
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightDialogClosed"
      >
      <!-- 树形控件 -->
      <el-tree ref="treeRef" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" :data="rightslist" :props="treeProps"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRights">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {
      // 所有角色列表数据
      rolelist: [],
      // 控制添加对话框的显示与隐藏
      addDialogVisible: false,
      // 添加角色的表单数据
      roleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 添加表单的验证规则
      addRoleRules: {
        roleName: [
          { required: true, message: '角色名不能为空', trigger: 'blur' },
          { min: 2, max: 10, message: '角色名长度在2-10个字符之间', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '角色描述不能为空', trigger: 'blur' },
          { min: 2, message: '角色描述长度最少2个字符', trigger: 'blur' }
        ]
      },
      // 修改角色提示框的隐藏与显示
      editDialogVisible: false,
      // 修改角色的数据
      editRoleForm: {},
      // 控制权限对话框的显示与隐藏
      setRightDialogVisible: false,
      // 所有权限数据
      rightslist: [],
      // 树形控件
      treeProps: {
        // 子健
        children: 'children',
        // 名称
        label: 'authName'
      },
      // 默认选中的权限
      defKeys: [],
      // 当前即将分配角色的id
      roleId: ''
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 获取所有角色列表
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')

      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败！')
      }

      this.rolelist = res.data
    },

    // addDialogClosed 用于关闭对话框之后清空数据
    addDialogClosed () {
      this.$refs.addRoleFormRef.resetFields()
    },

    // 提交数据 添加角色
    addrole () {
      // 判断是否通过验证
      this.$refs.addRoleFormRef.validate(async vaild => {
        if (!vaild) return
        // 发起请求 添加数据
        const { data: res } = await this.$http.post('roles', this.roleForm)
        if (res.meta.status !== 201) {
          return this.$message.error('添加角色失败')
        }

        // 隐藏对话框
        this.addDialogVisible = false
        // 查询获取数据
        this.getRolesList()
      })
    },

    // 修改角色
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      // 把获取到的数据添加回editRoleForm
      this.editRoleForm = res.data
      // 打开修改的对话框
      this.editDialogVisible = true
    },

    // 修改角色的对话框取消时 返回数据
    editDialogClosed () {
      this.$refs.editRoleFormRef.resetFields()
    },

    // 修改数据
    editRoleInfo () {
      this.$refs.editRoleFormRef.validate(async vaild => {
        if (!vaild) return
        const { data: res } = await this.$http.put('roles/' + this.editRoleForm.roleId, {
          roleName: this.editRoleForm.roleName,
          roleDesc: this.editRoleForm.roleDesc
        })
        if (res.meta.status !== 200) {
          return this.$message.error('角色修改失败')
        }
        this.$message.success('角色修改成功')
        // 关闭对话框
        this.editDialogVisible = false
        // 查询获取数据
        this.getRolesList()
      })
    },

    // 删除角色
    async removeRoleById (id) {
      const confirmResult = await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => {
        return err
      })

      if (confirmResult !== 'confirm') {
        return this.$message('已经取消删除')
      }

      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除角色失败')
      }
      this.$message.success('删除角色成功')
      this.getRolesList()
    },

    // 根据id删除对应的权限
    async removeRightById (role, rightId) {
      // 弹框提示是否要永久删除权限
      const confirmResult = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message('已经取消删除!')
      }

      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除权限失败!')
      }

      role.children = res.data
    },

    // 展示分配权限的对话框
    async showSetRightDialog (role) {
      this.roleId = role.id
      // 获取所有权限的数据
      const { data: res } = await this.$http.get('rights/tree')

      if (res.meta.status !== 200) {
        return this.$message.error('获取数据失败')
      }

      // 获取到的权限数据 保存到data中
      this.rightslist = res.data

      // 递归获取三级节点的ID
      this.getLeafKeys(role, this.defKeys)
      console.log(this.defKeys)

      this.setRightDialogVisible = true
    },

    // 通过递归的形式 获取角色下所有三级权限的id, 并保存到defKeys 数组中
    getLeafKeys (node, arr) {
      // 如果当前node节点不包含children(已经是最后一级了)属性 则是三级节点
      if (!node.children) {
        return arr.push(node.id)
      }

      node.children.forEach(item => this.getLeafKeys(item, arr))
    },

    // 监听分配权限对话框的关闭事件 情况权限
    setRightDialogClosed () {
      this.defKeys = []
    },

    //
    async allotRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]

      const idStr = keys.join(',')

      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })

      if (res.meta.status !== 200) {
        return this.$message.error('权限分配失败！')
      }

      this.$message.success('权限分配成功！')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag{
  margin: 10px;
}
.bdtop{
  border-top: 1px solid #eee;
}
.bdbottom{
  border-bottom:1px solid #eee;
}
.vcenter{
  display: flex;
  align-items: center;
}
</style>
